<template>
  <div class="app_AddKeyword">
    <el-row :gutter="20">
      <el-col :xs="24"
        :sm="22"
        :md="20"
        :lg="18"
        :xl="16">
        <el-form :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm add_form">

          <el-form-item label=""
            prop="gid">
            <el-select v-model="ruleForm.gid"
              placeholder="">
              <el-option v-for="(item, index, key) in groupAll"
                :key="key"
                :label="item.GroupName"
                :value="item.ID"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label=""
            prop="webID">
            <el-radio-group v-model="ruleForm.webID">
              <el-radio :label="1">PC</el-radio>
              <el-radio :label="2"></el-radio>
            </el-radio-group>
          </el-form-item>

          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label=""
                prop="key">
                <el-input class="addTextarea"
                  type="textarea"
                  v-model.trim="ruleForm.key"></el-input>
                <span class="form_hint"></span>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label=""
                prop="address">
                <el-input class="addTextarea"
                  type="textarea"
                  v-model.trim="ruleForm.address"></el-input>
                <span class="form_hint">baidu.com2zhidao.baidu.com</span>
              </el-form-item>
            </el-col>
          </el-row>

          <el-form-item label="">
            <el-input v-model="ruleForm.feature"></el-input>
            <span class="form_hint">2</span>
          </el-form-item>

          <el-form-item>
            <el-button type="primary"
              v-loading="loading"
              @click="submitForm('ruleForm')"> </el-button>
            <el-button @click="resetForm('ruleForm')"> </el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>

    <div class="tip">
      <p v-cloak>
        
        <span class="font-danger">{{userData.gradeName}}</span>,
        <span class="font-primary">{{userData.KeyMaxCount}}</span> 
        <span class="font-warning">{{userData.KeyMaxCount-userData.KeyCount}}</span> <br/> 5(3)
        <br/> 200
      </p>
    </div>
  </div>
</template>
<script>
import Util from '../assets/util'
export default {
  name: 'addKeyword',
  props: ['userData'],
  data () {
    let keyUrl = (rules, value, callback) => {
      Util.validate.keyUrl({ rules, value, callback }, this.keyArr, this.urlArr)
    }

    return {
      loading: false,
      groupAll: [],
      ruleForm: {
        gid: '',
        webID: 1,
        key: '',
        address: '',
        feature: ''
      },
      rules: {
        gid: [
          { required: true, message: '', trigger: 'change' }
        ],
        webID: [
          { required: true, message: '', trigger: 'change' }
        ],
        address: [
          { required: true, message: '', trigger: 'blur' },
          { validator: keyUrl, trigger: 'blur' }
        ],
        key: [
          { required: true, message: '', trigger: 'blur' },
          { validator: keyUrl, trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    keyArr () {
      return this.ruleForm.key.split('\n').map(function (n, i, arr) {
        return n.trim()
      }).filter(function (n, i, arr) {
        return n !== ''
      })
    },
    urlArr () {
      return this.ruleForm.address.split('\n').map(function (n, i, arr) {
        return n.trim()
      }).filter(function (n, i, arr) {
        return n !== ''
      })
    },
    keyUrl () {
      return this.keyArr.map((n, i, arr) => {
        return n + '\t' + this.urlArr[i] + '\n'
      }).join('')
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.loading = true
          for (let i = 0; i < this.keyArr.length; i++) {
            this.submitApi(this.keyArr[i] + '\t' + this.urlArr[i] + '\n')
          }
          setTimeout(() => {
            this.loading = false
            this.$message({
              message: '',
              type: 'success',
              duration: 800
            })
            this.ruleForm.feature = ''
            this.resetForm(formName)
          }, this.keyArr.length * 200)
        } else {
          return false
        }
      })
    },
    resetForm (formName) {
      this.ruleForm.feature = ''
      this.$refs[formName].resetFields()
    },
    selectInfo () {
      this.$http.post('/api/GroupALL', {
        userid: Util.user().id
      }).then(
        response => {
          if (response.status === 200 && response.ok) {
            this.groupAll = response.body
          } else {
            this.$message.error('')
          }
        },
        response => {
          this.$message.error('')
        }
      ).catch(response => {
        console.log(response)
      })
    },
    submitApi (mykey) {
      this.$http.post('/api/keyadd', {
        userid: Util.user().id,
        key: mykey,
        feature: this.ruleForm.feature,
        webID: this.ruleForm.webID,
        gid: this.ruleForm.gid
      }).then(
        response => {
          // if (response.status === 200 && response.ok) {
          //   if (response.body === 1) {
          //     this.$message({
          //       message: '',
          //       type: 'success',
          //       duration: 800
          //     })
          //   } else {
          //     this.$message({
          //       message: '',
          //       type: 'warning',
          //       duration: 800
          //     })
          //   }
          // } else {
          //   this.$message.error('')
          // }
        },
        response => {
          this.$message.error('')
        }
      ).catch(response => {
        console.log(response)
      })
    }
  },
  mounted: function () {
    this.selectInfo()
  }
}
</script>



