<template>
  <div class="app_aside">
    <el-menu class="el-menu-vertical-demo"
      mode="vertical"
      :collapse="asideSwitch"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#66b1ff"
      :default-active="$route.path.substring(1)"
      unique-opened
      menu-trigger="click"
      router
      collapse-transition>
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-setting"></i>
          <span slot="title"></span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="groupingmanagement"></el-menu-item>
          <el-menu-item index="keywordlist"></el-menu-item>
          <el-menu-item index="addkeyword"></el-menu-item>
          <el-menu-item index="recyclebin"></el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="2">
        <template slot="title">
          <i class="el-icon-document"></i>
          <span slot="title"></span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="rffect"></el-menu-item>
          <el-menu-item index="dayrecord"></el-menu-item>
          <el-menu-item index="monthrecord"></el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="3">
        <template slot="title">
          <i class="el-icon-star-on"></i>
          <span slot="title"></span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="membercenter"></el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
export default {
  name: 'appAside',
  props: ['asideSwitch'],
  data () {
    return {}
  }
}
</script>



