<template>
  <div class="app_GroupingManagement app_content_box">
    <div class="app_search">
      <el-button type="primary"
        icon="el-icon-plus"
        size="small"
        @click="operateG('add')"></el-button>
    </div>

    <el-table class="app_table"
      style="width: 100%"
      v-loading="loading"
      :data="tableData"
      height="calc(100% - 84px)"
      max-height="calc(100% - 84px)"
      border
      size="medium"
      :highlight-current-row="true">

      <el-table-column fixed
        type="index"
        label=""
        width="50">
      </el-table-column>

      <el-table-column prop="UserID"
        label="UserID"
        v-if=false>
      </el-table-column>

      <el-table-column prop="ID"
        label="ID"
        width="150">
      </el-table-column>

      <el-table-column prop="GroupName"
        label=""
        width="auto">
      </el-table-column>

      <el-table-column label=""
        width="300">
        <template slot-scope="scope">
          <el-progress v-if="scope.row.RndClickPer==100"
            status="success"
            :percentage="scope.row.RndClickPer"></el-progress>
          <el-progress v-else
            :percentage="scope.row.RndClickPer"></el-progress>
        </template>
      </el-table-column>

      <el-table-column fixed="right"
        label=""
        width="240">
        <template slot-scope="scope">
          <el-button v-if="scope.$index !== 0"
            type="text"
            size="mini"
            icon="el-icon-sort-up"
            circle
            title=""
            @click="operateG('up',scope.row)"></el-button>
          <el-button v-if="scope.$index !== tableData.length-1"
            type="text"
            size="mini"
            icon="el-icon-sort-down"
            circle
            title=""
            @click="operateG('down',scope.row)"></el-button>
          <el-button type="text"
            size="small"
            @click="operateG('edit',scope.row)"></el-button>
          <el-button type="text"
            size="small"
            @click="operateG('del',scope.row)"></el-button>
          <el-button type="text"
            size="small"
            @click="operateG('export',scope.row)"></el-button>
        </template>
      </el-table-column>

    </el-table>

    <el-dialog :title="dialogTitle"
      width="40%"
      append-to-body
      @close="closeDialog"
      :visible.sync="dialogGroup">
      <el-form :model="groupForm"
        :rules="rules"
        ref="groupForm">
        <el-form-item label=""
          :label-width="formLabelWidth"
          prop="groupName">
          <el-input v-model.trim="groupForm.groupName"
            auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer"
        class="dialog-footer">
        <el-button type="primary"
          @click="submitG('groupForm')"
          v-loading="loading2"> </el-button>
      </div>
    </el-dialog>

    <el-dialog title=""
      :visible.sync="dialogHint"
      width="30%">
      <span></span>
      <span slot="footer"
        class="dialog-footer">
        <el-button @click="dialogHint = false"> </el-button>
        <el-button type="primary"
          @click="groupApi(true)"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import Util from '../assets/util'
export default {
  name: 'groupingManagement',
  data () {
    return {
      loading: true,
      loading2: false,
      tableData: [],
      dialogHint: false,
      dialogGroup: false,
      dialogTitle: '',
      dialogInfo: {
        act: '',
        msg: '',
        groupID: 0
      },
      formLabelWidth: '120px',
      groupForm: {
        groupName: ''
      },
      rules: {
        groupName: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  methods: {
    operateG (way, row) {
      switch (way) {
        case 'up':
          this.dialogInfo = { act: 'up', msg: '', groupID: row.ID }
          this.groupApi(true)
          break
        case 'down':
          this.dialogInfo = { act: 'down', msg: '', groupID: row.ID }
          this.groupApi(true)
          break
        case 'add':
          this.dialogGroup = true
          this.dialogTitle = ''
          this.dialogInfo = { act: 'add', msg: '', groupID: 0 }
          break
        case 'edit':
          this.dialogGroup = true
          this.dialogTitle = ''
          this.dialogInfo = { act: 'edit', msg: '', groupID: row.ID }
          break
        case 'del':
          this.dialogHint = true
          this.dialogInfo = { act: 'del', msg: '', groupID: row.ID }
          break
        case 'export':
          if (row.ID) window.open('/api/keys_export?userid=' + Util.user().id + '&groupID=' + row.ID)
          break
        default:
          break
      }
    },
    closeDialog () {
      this.$refs.groupForm.clearValidate()
      this.groupForm.groupName = ''
      this.dialogGroup = false
    },
    groupApi (off) {
      this.loading2 = true
      this.$http.post('/api/group', {
        userid: Util.user().id,
        id: this.dialogInfo.groupID,
        groupName: this.groupForm.groupName,
        act: this.dialogInfo.act
      }).then(
        response => {
          if (response.status === 200 && response.ok) {
            if (response.body === '1') {
              this.$message({
                message: this.dialogInfo.msg + '',
                type: 'success',
                duration: 1000
              })
              off ? this.dialogHint = false : this.closeDialog()
              this.tableRefresh()
            } else if (response.body === '2') {
              this.$message({
                message: '' + this.dialogInfo.msg,
                type: 'warning',
                duration: 1000
              })
            } else {
              this.$message({
                message: this.dialogInfo.msg + '',
                type: 'warning',
                duration: 1000
              })
            }
          } else {
            this.$message.error('')
          }
        },
        response => {
          this.$message.error('')
        }
      ).catch(response => {
        console.log(response)
      }).finally(() => {
        this.loading2 = false
      })
    },
    submitG (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.groupApi()
        } else {
          return false
        }
      })
    },
    tableRefresh () {
      this.loading = true
      this.$http.post('/api/group_list', {
        userid: Util.user().id
      }).then(
        response => {
          if (response.status === 200 && response.ok) {
            this.tableData = response.body.data
          } else {
            this.$message.error('')
          }
        },
        response => {
          this.$message.error('')
        }
      ).catch(response => {
        console.log(response)
      }).finally(() => {
        this.loading = false
      })
    }
  },
  mounted () {
    setTimeout(() => {
      this.tableRefresh()
    }, 300)
  }
}
</script>



