<template>
  <div class="app_login">
    <div class="app_login_mask"></div>
    <el-form :model="ruleForm"
      :rules="rules2"
      ref="ruleForm"
      label-position="left"
      label-width="0px"
      class="demo-ruleForm login-container">

      <h3 class="title"></h3>

      <el-form-item prop="account">
        <el-input type="text"
          v-model="ruleForm.account"
          auto-complete="off"
          placeholder=""></el-input>
      </el-form-item>

      <el-form-item prop="pass">
        <el-input type="password"
          v-model="ruleForm.pass"
          auto-complete="off"
          placeholder=""></el-input>
      </el-form-item>

      <el-form-item prop="verifycode">
        <el-input type="text"
          v-model="ruleForm.verifycode"
          auto-complete="off"
          placeholder="">
          <img :src="verifycodeUrl"
            alt=""
            style="width: 90px;height: 80%;margin-top: 4%;cursor: pointer;"
            slot="suffix"
            @click="getCodeKey">
        </el-input>
      </el-form-item>

      <el-checkbox v-model="checked"
        class="remember"></el-checkbox>

      <el-form-item style="width:100%;">
        <el-button type="primary"
          style="width:100%;"
          @click.native.prevent="handleSubmit2"
          :loading="logining"></el-button>
      </el-form-item>

      <el-form-item class="text-right register">
        <span class="text"></span>
        <router-link to="/register"
          class="buttonText"></router-link>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      logining: false,
      verifycodeUrl: 'api/GetVerifyCode',
      ruleForm: {
        account: sessionStorage.getItem('sessionA') ? decodeURI(sessionStorage.getItem('sessionA')) : '',
        pass: sessionStorage.getItem('sessionB') ? decodeURI(sessionStorage.getItem('sessionB')) : '',
        verifycode: ''
      },
      checked: true,
      rules2: {
        account: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, message: ' 4 ', trigger: 'blur' }
        ],
        pass: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: ' 6 ', trigger: ['blur', 'change'] }
        ],
        verifycode: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 5, message: ' 5 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    handleSubmit2 () {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.logining = true
          this.$http.post('/api/Login', {
            username: this.ruleForm.account,
            password: this.ruleForm.pass,
            checkCode: this.ruleForm.verifycode
          }).then(response => {
            if (response.status === 200 && response.ok) {
              console.log(response)
              if (response.body.indexOf('1') !== -1) {
                this.$message({
                  message: '',
                  type: 'success',
                  duration: 800
                })
                if (this.checked) {
                  sessionStorage.setItem('sessionA', encodeURI(this.ruleForm.account))
                } else {
                  sessionStorage.removeItem('sessionA')
                }
                sessionStorage.setItem('sessionID', encodeURI('-' + Math.random() + '-' + response.body.split('|')[1] + ''))
                setTimeout(() => {
                  this.$router.push({ path: '/membercenter' })
                }, 600)
              } else if (response.body === '2') {
                this.$message({
                  message: '',
                  type: 'warning'
                })
                this.ruleForm.pass = ''
              } else if (response.body === '3') {
                this.$message({
                  message: '',
                  type: 'warning'
                })
                this.ruleForm.verifycode = ''
              } else {
                this.$message.error('')
              }
            }
          }, response => {
            this.$message.error('')
          }).catch(response => {
            console.log(response)
          }).finally(() => {
            this.logining = false
          })
        } else {
          return false
        }
      })
    },
    getCodeKey () {
      this.verifycodeUrl = 'api/GetVerifyCode?t=' + new Date().getTime()
    }
  },
  mounted () {
    sessionStorage.removeItem('sessionB')
  }
}
</script>

<style scoped>
.login-container .remember {
  margin: 0px 0px 35px 0px;
}
</style>



