'use strict'
// import $ from 'jquery'

export default {
  //  id
  user () {
    let idArr = decodeURI(sessionStorage.getItem('sessionID')).replace(/[\u4E00-\u9FA5]+/g, '').split('-')
    return {
      id: idArr[idArr.length - 1]
    }
  },
  //  
  validate: {
    keyUrl ({
      rule,
      value,
      callback
    }, keyArr, urlArr) {
      if ((urlArr.length > 0) && (keyArr.length > 0) && keyArr.length !== urlArr.length) {
        callback(new Error('!'))
      } else {
        callback()
      }
    },
    checkPass ({
      rule,
      value,
      callback
    }, oldpass) {
      if (value !== oldpass) {
        callback(new Error('!'))
      } else {
        callback()
      }
    },
    phone (rule, value, callback) {
      const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
      if (!reg.test(value)) {
        callback(new Error('11'))
      } else {
        callback()
      }
    },
    namePass (rule, value, callback) {
      const reg = /^[a-zA-Z0-9_@]{1,}$/
      if (!reg.test(value)) {
        callback(new Error('@'))
      } else {
        callback()
      }
    },
    qq (rule, value, callback) {
      const reg = /^[1-9]\d{4,8}$/
      if (!reg.test(value) && value !== '') {
        callback(new Error('qq'))
      } else {
        callback()
      }
    },
    truename (rule, value, callback) {
      const reg = /^[\u4E00-\u9FA5\uf900-\ufa2ds]{2,20}$/
      if (!reg.test(value) && value !== '') {
        callback(new Error(''))
      } else {
        callback()
      }
    }
  },
  //  
  timestampToTime (timestamp) {
    let date = new Date(timestamp)
    return date.getFullYear() + '-' + (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-' + date.getDate() + ' ' + date.getHours() + ':' + date.getMinutes() + ':' + date.getSeconds()
  }
}



