<template>
  <div class="app_KeywordList app_content_box">
    <div class="app_search clearfix">
      <div class="left">
        <el-button-group>
          <el-button type="primary"
            size="small"
            @click="dialogOpen(1,'')"></el-button>
          <el-button type="primary"
            size="small"
            v-loading="loading2"
            @click="keysstop"></el-button>
        </el-button-group>
      </div>
      <div class="app_search_box text-right">
        <el-form :inline="true"
          :model="formInline"
          size="small"
          class="demo-form-inline">
          <el-form-item label="">
            <el-input v-model="formInline.key"
              placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="formInline.webID"
              placeholder="">
              <el-option label=""
                value=""></el-option>
              <el-option label="PC"
                :value="1"></el-option>
              <el-option label=""
                :value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="formInline.groupID"
              width="100"
              placeholder="">
              <el-option label=""
                value=""></el-option>
              <el-option v-for="(item, index, key) in groupAll"
                :key="key"
                :label="item.GroupName"
                :value="item.ID"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary"
              icon="el-icon-search"
              size="small"
              @click="onSearch"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>

    <el-table class="app_table"
      style="width: 100%"
      v-loading="loading"
      ref="tableData"
      :data="tableData"
      @selection-change="changeFun"
      @sort-change="sortFun"
      height="calc(100% - 218px)"
      max-height="calc(100% - 218px)"
      border
      size="medium"
      :highlight-current-row="true">

      <el-table-column fixed="left"
        type="selection"
        width="55">
      </el-table-column>

      <el-table-column type="index"
        :index="typeIndex"
        label=""
        width="50">
      </el-table-column>

      <el-table-column prop="KeyValue"
        label=""
        min-width="150">
      </el-table-column>

      <el-table-column label=""
        min-width="100">
        <template slot-scope="scope">
          <span v-if="groupAll.length<=0">
            
          </span>
          <span v-else
            v-for="(item, index, key) in groupAll"
            :key="key">
            <b v-if="item.ID===scope.row.GroupID">{{item.GroupName}}</b>
          </span>
        </template>
      </el-table-column>

      <el-table-column label=""
        min-width="100">
        <template slot-scope="scope">
          <span v-if="scope.row.WebID===1">PC</span>
          <span v-else-if="scope.row.WebID===2"></span>
          <span v-else></span>
        </template>
      </el-table-column>

      <el-table-column label=""
        min-width="250">
        <template slot-scope="scope">
          <a :href="'http://'+scope.row.Address"
            target="_blank"
            class="buttonText">{{scope.row.Address}}</a>
        </template>
      </el-table-column>

      <el-table-column prop="BaiduIndex"
        label=""
        width="100">
      </el-table-column>

      <el-table-column prop="Price"
        label=""
        min-width="80">
      </el-table-column>

      <el-table-column label=""
        min-width="80">
        <template slot-scope="scope">
          <div v-html="scope.row.TodayPrice"></div>
        </template>
      </el-table-column>

      <el-table-column label=""
        min-width="100">
        <template slot-scope="scope">
          <div v-html="scope.row.StatusHtml"></div>
        </template>
      </el-table-column>

      <el-table-column prop="UpdateTime"
        label=""
        min-width="100">
      </el-table-column>

      <el-table-column prop="StartRank"
        label=""
        min-width="80"
        sortable="custom">
      </el-table-column>

      <el-table-column prop="NewRank"
        label=""
        min-width="80"
        sortable="custom">
      </el-table-column>

      <el-table-column prop="EffectValue"
        label=""
        min-width="80"
        sortable="custom">
      </el-table-column>

      <el-table-column prop="CreateTime"
        label=""
        min-width="100">
      </el-table-column>

      <el-table-column prop="SuccessTime"
        label=""
        min-width="110">
      </el-table-column>

      <el-table-column prop="PayQty"
        label=""
        min-width="110"
        sortable="custom">
      </el-table-column>

      <el-table-column fixed="right"
        label=""
        width="100">
        <template slot-scope="scope">
          <el-button type="text"
            size="small"
            @click="keyinfo(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <div class="app_pages">
      <el-pagination @current-change="CurrentChange"
        :current-page="currentPage"
        :page-size="100"
        layout="total, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <div class="tip">
      <p v-cloak>
        <span class="font-danger">{{userData.gradeName}}</span> ,
        <span class="font-primary">{{userData.KeyMaxCount}}</span> 
        <span class="font-warning">{{userData.KeyMaxCount-userData.KeyCount}}</span> </p>
    </div>

    <el-dialog :title="dialogTitle"
      width="30%"
      append-to-body
      @close="closeDialog"
      :visible.sync="dialogBtn">
      <el-form :model="btnForm"
        :rules="rules"
        ref="btnForm">
        <el-form-item v-if="catGroup ===1"
          label=""
          :label-width="formLabelWidth"
          prop="info">
          <el-select v-model="btnForm.info"
            style="width:100%"
            placeholder="">
            <el-option v-for="(item, index, key) in groupAll"
              :key="key"
              :label="item.GroupName"
              :value="item.ID"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer"
        class="dialog-footer">
        <el-button type="primary"
          @click.native.prevent="submitG('btnForm')"
          v-loading="loading3"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import Util from '../assets/util'
export default {
  name: 'keywordList',
  props: ['userData'],
  data () {
    return {
      loading: true,
      loading2: false,
      loading3: false,
      tableData: [],
      tableDESC: 11,
      currentPage: 1,
      total: 0,
      groupAll: [],
      selectID: [],
      formInline: {
        key: '',
        webID: '',
        groupID: ''
      },
      dialogBtn: false,
      dialogTitle: '',
      catGroup: 1,
      formLabelWidth: '120px',
      btnForm: {
        info: ''
      },
      rules: {
        info: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  methods: {
    onSearch () {
      this.tableRefresh()
    },
    typeIndex (index) {
      return index + (this.currentPage - 1) * 10 + 1
    },
    CurrentChange (val) {
      this.currentPage = val
      this.tableRefresh()
    },
    tableRefresh () {
      this.loading = true
      this.$http.post('/api/keys', {
        userid: Util.user().id,
        page: this.currentPage,
        groupID: this.formInline.groupID,
        webID: this.formInline.webID,
        orderType: this.tableDESC,
        key: this.formInline.key
      }).then(
        response => {
          if (response.status === 200 && response.ok) {
            this.tableData = response.body.data.map(function (item, key, ary) {
              for (let dateI in item) {
                if (typeof item[dateI] === 'string' && item[dateI].indexOf('/Date(') !== -1) {
                  item[dateI] = Util.timestampToTime(parseInt(item[dateI].replace(/[^0-9.]/g, '')))
                }
              }
              return item
            })
            this.total = response.body.total
          } else {
            this.$message.error('')
          }
        },
        response => {
          this.$message.error('')
        }
      ).catch(response => {
        console.log(response)
      }).finally(() => {
        this.loading = false
      })
    },
    selectInfo () {
      this.$http.post('/api/GroupALL', {
        userid: Util.user().id
      }).then(
        response => {
          if (response.status === 200 && response.ok) {
            this.groupAll = response.body
          } else {
            this.$message.error('')
          }
        },
        response => {
          this.$message.error('')
        }
      ).catch(response => {
        console.log(response)
      })
    },
    keyinfo (data) {
      console.log(data)
    },
    changeFun (val) {
      let arr = val.map(function (item, index, arr) {
        return item.ID
      })
      this.selectID = arr.join(',')
    },
    sortFun (column) {
      console.log(column)
      if (column.prop === 'StartRank') {
        column.order === 'ascending' ? this.tableDESC = 15 : this.tableDESC = 16
      } else if (column.prop === 'NewRank') {
        column.order === 'ascending' ? this.tableDESC = 11 : this.tableDESC = 12
      } else if (column.prop === 'EffectValue') {
        column.order === 'ascending' ? this.tableDESC = 14 : this.tableDESC = 13
      } else if (column.prop === 'SuccessFlag') {
        column.order === 'ascending' ? this.tableDESC = 18 : this.tableDESC = 17
      } else if (column.prop === 'PayQty') {
        column.order === 'ascending' ? this.tableDESC = 20 : this.tableDESC = 19
      } else {
        this.tableDESC = 11
      }
      this.tableRefresh()
    },
    keysstop () {
      if (this.selectID.length <= 0) {
        this.$message({
          message: '',
          type: 'warning',
          duration: 800
        })
        return false
      }
      this.loading2 = true
      this.$http.post('/api/keysstop', {
        userid: Util.user().id,
        ids: this.selectID
      }).then(
        response => {
          if (response.status === 200 && response.ok) {
            this.$message({
              message: '',
              type: 'success',
              duration: 800
            })
            this.tableRefresh()
          } else {
            this.$message.error('')
          }
        },
        response => {
          this.$message.error('')
        }
      ).catch(response => {
        console.log(response)
      }).finally(() => {
        this.loading2 = false
      })
    },
    submitG (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let url, json
          if (this.catGroup === 1) {
            url = '/api/setkeysgroup'
            json = {
              userid: Util.user().id,
              groupID: this.btnForm.info,
              ids: this.selectID
            }
          } else if (this.catGroup === 2) {
            url = '/api/setkeyscat'
            json = {
              userid: Util.user().id,
              catID: this.btnForm.info,
              ids: this.selectID
            }
          } else {
            return false
          }
          this.loading3 = true
          this.$http.post(url, json).then(
            response => {
              if (response.status === 200 && response.ok) {
                this.$message({
                  message: '',
                  type: 'success',
                  duration: 800
                })
                this.tableRefresh()
                this.closeDialog()
              } else {
                this.$message.error('')
              }
            },
            response => {
              this.$message.error('')
            }
          ).catch(response => {
            console.log(response)
          }).finally(() => {
            this.loading3 = false
          })
        } else {
          return false
        }
      })
    },
    dialogOpen (num, title) {
      if (this.selectID <= 0) {
        this.$message({
          message: '',
          type: 'warning',
          duration: 800
        })
      } else {
        this.dialogBtn = true
        this.catGroup = num
        this.dialogTitle = title
      }
    },
    closeDialog () {
      this.$refs.btnForm.clearValidate()
      this.btnForm.info = ''
      this.dialogBtn = false
    }
  },
  mounted: function () {
    this.selectInfo()
    setTimeout(() => {
      this.tableRefresh()
    }, 300)
  }
}
</script>



